import styled, { css } from 'styled-components';

import Tooltip from '../Tooltop';

interface ContainerProps {
  isFocused: boolean;
  isFilled: boolean;
  isErrored: boolean;
}

export const Container = styled.div<ContainerProps>`
  background: #232129;
  border-radius: 10px;
  padding: 16px;
  width: 100%;
  display: flex;
  align-items: center;

  border: 2px solid #232129;
  color: #666360;

  & + div {
    margin-top: 8px;
  }
  ${props =>
    props.isErrored &&
    css`
      border-color: #c53030;
    `}

  ${props =>
    props.isFocused &&
    css`
      color: #ff5100;
      border-color: #ff5100;
    `}

  ${props =>
    props.isFilled &&
    css`
      color: #ff5100;
    `}



  input {
    flex: 1;
    border: 0;
    color: #f4ede8;
    background: transparent;
    &::placeholder {
      color: #666360;
    }
  }
  svg {
    margin-right: 16px;
  }
`;
export const Error = styled(Tooltip)`
  height: 20px;
  margin-left: 16px;
  svg {
    margin: 0;
  }
  span {
    background: #c53030;
    color: #f1f1f1;

    &::before {
      border-color: #c53030 transparent;
    }
  }
`;
